:root {
    --background-color-1 : rgb(0, 0, 0);
    --text-color-1 : rgb(197, 189, 117);
}


.intro-preload{
    cursor: pointer;
}

.intro{
    width: 100vw;
    height: 100vh;
    background-color: var(--background-color-1);
}

.intro-bck-image{
    position: absolute;
    width: 100vw;
    height: 100vh;
    background: url("../../img/introBackground.jpg") ;
    background-repeat: no-repeat;
    background-size: 200vw 200vh;
    background-position: 0px 0px;
    filter:opacity(30%);
    animation-name: intro-bck-image-anim;
    animation-duration: 200s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}

@keyframes intro-bck-image-anim {
    0%{
        background-size: 200vw 200vh;
        background-position: 0px 0px;
    }
    20%{
        background-size: 180vw 190vh;
        background-position: -20vw -10vh;
    }
    40%{
        background-size: 190vw 170vh;
        background-position: -10vw -40vh;
    }
    60%{
        background-size: 210vw 200vh;
        background-position: -30vw 0vh;
    }
    80%{
        background-size: 240vw 220vh;
        background-position: -40vw -20vh;
    }
    100%{
        background-size: 200vw 200vh;
        background-position: 0px 0px;
    }
    
}

.intro-preload{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 100%;
    min-height: 100vh;
}

.intro-txt{
    color: var(--text-color-1);
    text-align: center;
    padding: 0 20px;
    opacity: 0;
    margin-top: 10%;
    animation-delay: 1s;
    text-shadow: 0 8px 2px rgb(19, 13, 1);
}

.intro-txt-2{
    animation-delay: 4s;
    margin-top: 50px;
    color: rgb(177, 71, 9);
}

.intro-txt-3{
    animation-delay: 7s;
    margin-top: 50px;
}

.intro-bottom-txt{
    position: absolute;
    text-align: center;
    bottom: 120px;
    color: rgb(177, 71, 9);
    font-size: 1.3rem;
}

.text-show-animation{
    animation-name: text-show-anim;
    animation-fill-mode: forwards;
    animation-duration: 1s;
    animation-timing-function:ease-in-out;
}

.text-show-animation-2{
    animation-name: text-show-anim-2;
    animation-fill-mode: forwards;
    animation-duration: 1s;
    animation-timing-function:ease-in-out;
}

.text-show-animation-3{
    animation-name: text-show-anim-3;
    animation-fill-mode: forwards;
    animation-duration: 1s;
    animation-timing-function:ease-in-out;
}

@keyframes text-show-anim {
    0%{
        font-size: 0.1rem;
        filter: blur(2rem);
        opacity: 0;
    }
    5%{
        filter: blur(1.5rem);
        opacity: 0.5;
    }
    100%{
        opacity: 1;
        font-size:2rem;
        filter: blur(0rem);
    }
}

@keyframes text-show-anim-2 {
    0%{
        font-size: 0.1rem;
        filter: blur(2rem);
        opacity: 0;
    }
    5%{
        filter: blur(1.5rem);
        opacity: 0.5;
    }
    100%{
        opacity: 1;
        font-size: 2.5rem;
        filter: blur(0rem);
    }
}

@keyframes text-show-anim-3 {
    0%{
        font-size: 0.1rem;
        filter: blur(2rem);
        opacity: 0;
    }
    5%{
        filter: blur(1.5rem);
        opacity: 0.5;
    }
    100%{
        opacity: 1;
        font-size: 2rem;
        filter: blur(0rem);
    }
}

.flashing-animation{
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: flashing-anim;
}

@keyframes flashing-anim {
    0%{
        opacity: 0;
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}



@media screen and (max-width:800px) {

    @keyframes text-show-anim {
        0%{
            font-size: 0.1rem;
            filter: blur(2rem);
            opacity: 0;
        }
        5%{
            filter: blur(1.5rem);
            opacity: 0.5;
        }
        100%{
            opacity: 1;
            font-size:1.7rem;
            filter: blur(0rem);
        }
    }
    
    @keyframes text-show-anim-2 {
        0%{
            font-size: 0.1rem;
            filter: blur(2rem);
            opacity: 0;
        }
        5%{
            filter: blur(1.5rem);
            opacity: 0.5;
        }
        100%{
            opacity: 1;
