            font-size: 0.1rem;
            filter: blur(2rem);
            opacity: 0;
        }
        5%{
            filter: blur(1.5rem);
            opacity: 0.5;
        }
        100%{
            opacity: 1;
            font-size: 1.7rem;
            filter: blur(0rem);
        }
    }
    
}





