        0%{
            font-size: 2rem;
            filter: blur(2rem);
            opacity: 0;
        }
        100%{
            opacity: 1;
            font-size:2rem;
            filter: blur(0rem);
        }
    }
    
    @keyframes text-show-anim-2 {
        0%{
            font-size: 3rem;
            filter: blur(2rem);
            opacity: 0;
        }
        100%{
            opacity: 1;
